// Helper to compute counts on demand
export const getCategoryCounts = () => {
  return categories.map((c) =>
    c.id === "all"
      ? { ...c, count: streetFoods.length }
      : {
          ...c,
          count: streetFoods.filter((f) => f.category === c.id).length,
        }
  );
};

// Optional: Promise-based service API (handy if you used it earlier)
export const productService = {
  getProducts: async () => {
    // simulate API latency
    return new Promise((res) => setTimeout(() => res(streetFoods), 200));
  },
  getProductById: async (id) => {
    return new Promise((res) =>
      setTimeout(() => res(streetFoods.find((p) => p.id === Number(id)) || null), 150)
    );
  },
  searchProducts: async (query) => {
    const q = String(query || "").toLowerCase();
    return new Promise((res) =>
      setTimeout(
        () =>
          res(
            streetFoods.filter(
              (p) =>
                p.name.toLowerCase().includes(q) ||
                p.description.toLowerCase().includes(q)
            )
          ),
        150
      )
    );
  },
  getProductsByCategory: async (cat) => {
    return new Promise((res) =>
      setTimeout(
        () =>
          res(
            cat && cat !== "all"
              ? streetFoods.filter((p) => p.category === cat)
              : streetFoods
          ),
        150
      )
    );
  },
};
